/* eslint-disable react/jsx-props-no-spreading */
import React from 'react';
import Form, { FormComponentProps } from 'antd/lib/form';
import {
    Input, Button, notification, DatePicker,
} from 'antd';
import { AxiosResponse } from 'axios';
import Headers from '../component/headers';
import '../component/header.scss';
import api from '../../../common/utils/apiClient';
import { HTTP_OK } from '../../../common/constants/httpStatusCodes';

interface Props extends FormComponentProps {
    projectId: string;
    crnId: string;
    onSubmit(): void;
}

const formItemLayout = {
    labelCol: {
        xs: { span: 24 },
        sm: { span: 6 },
    },
    wrapperCol: {
        xs: { span: 24 },
        sm: { span: 14 },
    },
};

const tailWrapperLayout = {
    wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 },
    },
};

const CallForm = (props: Props): JSX.Element => {
    const {
        onSubmit, form: { getFieldDecorator, validateFieldsAndScroll },
    } = props;

    const handleSubmit = (e): void => {
        e.preventDefault();
        validateFieldsAndScroll((err, values) => {
            if (!err) {
                // console.log('Received values of form: ', values);
                // console.log(values.scheduleDate._d);

                const data = {
                    name: values.name,
                    phone: values.phone,
                    email: values.email,
                    scheduleDate: values.scheduleDate._d,
                    scheduleType: 'callback',
                };
                const payload = JSON.stringify(data);
                // console.log(payload);
                api.post('https://crm.bricknbolt.com/schedule', {}, payload).then((response: AxiosResponse) => {
                    if (response.status === HTTP_OK) {
                        notification.success({
                            message: 'Your concern/query is successfully registered with us, Our Project Manager will get in touch with you.',
                        });
                    } else {
                        notification.error({
                            message: 'Unable to create contract, try after sometime',
                        });
                    }
                });
                onSubmit();
            }
        });
    };
    const config = {
        rules: [{ type: 'object', required: true, message: 'Please select time!' }],
    };

    return (
        <>
            <Headers />
            <div className="CallnMeeting">

                <h2>Comfortable date and time to call you back</h2>

                <Form {...formItemLayout} onSubmit={handleSubmit} className="Form">

                    <Form.Item label="Name">
                        {getFieldDecorator('name', {
                            rules: [{ required: true, message: 'Required' }],
                        })(
                            <Input />,
                        )}
                    </Form.Item>
                    <Form.Item label="Phone">
                        {getFieldDecorator('phone', {
                            rules: [{ required: true, message: 'Required' }],
                        })(
                            <Input />,
                        )}
                    </Form.Item>
                    <Form.Item label="Email:">
                        {getFieldDecorator('Email', {
                            rules: [{ required: false, message: 'Required' }],
                        })(
                            <Input type="email" />,
                        )}
                    </Form.Item>
                    <Form.Item label="Calender">
                        {getFieldDecorator('date-time-picker', config)(
                            <DatePicker showTime format="YYYY-MM-DD HH:mm:ss" />,
                        )}
                    </Form.Item>


                    <Form.Item {...tailWrapperLayout} style={{ textAlign: 'center' }}>
                        <Button type="danger" htmlType="submit" className="button">Submit</Button>
                    </Form.Item>
                </Form>
            </div>
        </>
    );
};

export default Form.create<Props>({ name: 'Call-form' })(CallForm);
